.btn, .cmn--btn {
  color: $white;
  padding: 12px 35px;
  font-size: 18px;
  font-weight: 600;
  font-family: $heading;
  outline: none !important;
  box-shadow: none !important;
  @include transition;
  border-radius: 10px;
  z-index: 1;
  &:hover {
      color: $white;
  }
  &.btn--lg {
      padding: 13px 35px !important;
      font-size: 22px;
      @include breakpoint(max-lg) {
          font-size: 20px;
      }
      @include breakpoint(max-sm) {
          font-size: 18px;
      }
  }
  &.btn--md {
      padding: 8px 25px !important;
      font-size: 15px;
  }
  &.btn--sm {
      padding: 5px 15px !important;
      font-size: 14px;
  }
  &.btn--xs {
      padding: 0px 10px !important;
      font-size: 13px;
  }
}
.cmn--btn {
  position: relative;
  background: $gradient-one;
  &::before, &::after {
      position: absolute;
      content: '';
      height: 100%;
      width: 100%;
      z-index: -1;
      top: 0;
      @include transition;
  }
  &:hover, &.active {
      &::before {
          top: -100%;
      }
  }
}


.video-button {
    position: relative;
    font-size: 24px;
    width: 60px;
    height:60px;
    background: $gradient-one;
    border-radius: 50%;
    @extend %flex-center;
    display: inline-flex;
    color: $white;
    z-index: 1;
    &::before {
        @extend %position-set;
        border-radius: 50%;
        background: $gradient-one;
        z-index: -1;
        animation: play-button-two 2s ease-in infinite;
    }
    &::after {
        @extend %position-set;
        border-radius: 50%;
        background: $gradient-one;
        z-index: -1;
        animation: play-button-two 3s ease-in infinite;
        animation-delay: 1.2s;
    }

}







