.btn--capsule {
  border-radius: 99px;
}
.icon-btn {
  width: rem(30px);
  height: rem(30px);
  background-color: $base-color;
  @extend %text-white;
  border-radius: 3p;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  &:hover {
    @extend %text-white;
  }
}
.btn--group {
  @extend %flex;
  align-items: center;
  margin-left: rem(-10px);
  margin-right: rem(-10px);
  &.style--two {
    margin-left: rem(-5px);
    margin-right: rem(-5px);
    *[class*="btn"] {
      margin: rem(3px) rem(5px);
    }
  }
}
*[class*="btn"] {
  margin: rem(5px) rem(10px);
  align-items: center;
  &.d-flex {
    padding: rem(8px) rem(35px);
  }
}
[class*="btn"] {
  &.btn-md {
    padding: rem(10px) rem(20px);
    font-size: rem(15px);
  }
  &.btn-sm {
    padding: rem(6px) rem(10px);
    font-size: rem(14px);
  }
}
